.Statistics {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
    width: max-content;
    height: max-content;
    background-color: white;
}

.Title {
    justify-content: center;
    margin: 0;
    display: flex;

    font-size: 20px;
    line-height: 3;
}

.StatList {
    padding: 0;
    margin: 0;
    display: flex;
    width: inherit;
}

.Item {
    align-items: center;
    justify-content: center;
    height: 70px;
    width: 70px;
    display: flex;
    flex-direction: column;
    background-color: rgb(203, 115, 117);
}

.Label {
    font-size: 13px;
    margin-bottom: 10px;
}

.Percentage {
    font-size: 20px;
}
